export const MenuIcon = ({ className }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    width="37"
    height="30"
    fill="none"
    className={className}
    viewBox="0 0 37 30"
  >
    <path
      fill="url(#a)"
      d="M0 0h37v30H0z"
      transform="matrix(-1 0 0 1 37 0)"
    ></path>
    <defs>
      <pattern
        id="a"
        width="1"
        height="1"
        patternContentUnits="objectBoundingBox"
      >
        <use xlinkHref="#b" transform="matrix(.00237 0 0 .00292 -.007 0)"></use>
      </pattern>
      <image
        xlinkHref="data:image/png;base64,"
        id="b"
        width="428"
        height="342"
        preserveAspectRatio="none"
      ></image>
    </defs>
  </svg>
);
